/* Core */
import { Component, ChangeDetectorRef, NgZone, OnInit } from '@angular/core';
import { Observable } from 'rxjs/Rx';
import { IonicPage, NavController, NavOptions } from 'ionic-angular';
import { DomSanitizer } from '@angular/platform-browser';

/* Models */
import { GameWrapper } from '../../app/models/game-wrapper.model';

/* Services */
import { GamesService } from '../../app/services/games.service';

@IonicPage({
  name: 'games',
  segment: 'games'
})
@Component({
  selector: 'page-games',
  templateUrl: 'games.html'
})
export class GamesPage implements OnInit {
  public gameWrappers: GameWrapper[] = [];
  public currentPage: GamesPage;

  constructor(public navCtrl: NavController, private gamesService: GamesService, private ngZone: NgZone) {}

  ngOnInit(): void {
    this.getTopGames();
  }

  getTopGames() {
    let that = this;
    let promise;

    this.gamesService.getTop().then(function(gameWrappers: GameWrapper[]) {
      that.ngZone.run(() => {
        gameWrappers.forEach(function(gameWrapper) {
          that.gameWrappers.push(gameWrapper);
        });
      });
    });
  }
}



